<template>
  <div>
    <Header title="注册"></Header>
    <div class="tableContainer">
      <div class="tableRow">
        <p>平台名称</p>
        <p><input v-model="userInfo.name" class="side" placeholder="平台名称" type="text"></p>
      </div>
      <div class="tableRow">
        <p>平台邮箱</p>
        <p><input v-model="userInfo.email" class="side" placeholder="平台邮箱" type="text"></p>
      </div>
      <div class="tableRow">
        <p>备案网址</p>
        <p><input v-model="userInfo.site" class="side" placeholder="备案网址" type="text"></p>
      </div>
      <div class="tableRow">
        <p>备案域名</p>
        <p><input v-model="userInfo.domainname" class="side" placeholder="备案域名" type="text"></p>
      </div>
      <div class="tableRow">
        <p>备案号</p>
        <p><input v-model="userInfo.recordno" class="side" placeholder="备案号" type="text"></p>
      </div>
      <div class="tableRow">
        <p>经营范围</p>
        <p><input v-model="userInfo.businessscope" class="side" placeholder="经营范围" type="text"></p>
      </div>
      <div class="tableRow">
        <p>注册密码</p>
        <p><input v-model="userInfo.password" class="side" placeholder="注册密码" type="text"></p>
      </div>
      <div class="tableRow">
        <p>确认密码</p>
        <p><input v-model="userInfo.passwordAgain" class="side" placeholder="确认密码" type="text"></p>
      </div>
      <!--            <div class="tableRow item">-->
      <!--                <p></p>-->
      <!--                <p><input id="button" type="button" @click="register" value="立即注册"></p>-->
      <!--            </div>-->
      <div id="button">
        <input class="item" type="button" value="注册" @click="register">
        <input class="item" type="button" value="去登录" @click="toLogin">
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
    import Header from '../../components/Header'
    import Footer from '../../components/Footer'
    export default {
        name: 'register',
        components: {
            Header,
            Footer,
        },

        data: function () {
            return {
                userInfo:{
                    name: '',
                    email: '',
                    site: '',
                    domainname: '',
                    recordno: '',
                    businessscope: '',
                    password: '',
                    passwordAgain: '',
                }
            }
        },
        methods: {
            register: function () {
                if (!this.userInfo.name || !this.userInfo.email || !this.userInfo.site || !this.userInfo.domainname || !this.userInfo.recordno || !this.userInfo.businessscope
                        || !this.userInfo.password || !this.userInfo.passwordAgain) {
                    this.msgError("信息都不能为空");
                    return ;
                }
                if (this.userInfo.password!==this.userInfo.passwordAgain) {
                    this.msgError("二次密码不正确");
                    return ;
                }
                this.$request({
                    url:'/user/register',
                    method:'post',
                    data:this.userInfo,
                }).then((response) => {
                    this.msgSuccess(response.msg);
                });
                this.userInfo={};
            },
          toLogin: function (){
              this.$router.push({ path: '/index' })
          }
        },
    }
</script>

<style>
    .tableContainer{
        font-size: 20px;
        display: table;
        margin-left: auto;
        margin-right: auto;
    }
    .tableRow{
        display: table-row;
    }
    .tableRow p{
        display: table-cell;
        padding: 15px;
    }
    .tableRow p:last-child{
        font-size: 20px;
        padding: 20px;
    }
    .side{
        font-size: 18px;
        padding: 5px;
    }
    #button{
        text-align: center;
    }

    .item {
        width: 100%;
        height: 50px;
        border: none;
        margin: 10px auto 5px auto;
        background-color: #0c4469;
        color: white;
        text-align: center;
        border-radius: 5px;
        font-size: 20px;
        letter-spacing: 2px;
    }
</style>



